.loading__background {
	position: fixed;
	width: 100%;
	top: 0px;
	bottom: 0px;
	z-index: 1070;
}

.loading__backdrop {
	height: 100%;
	background-color: #000;
	opacity: 0.5;
}

.loading__box {
	height: 100%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
}

.loading__icon {
	position: relative;
	width: 200px;
	height: 200px;
	border: 20px solid #868e96;
	border-top-color: #007bff;
	border-radius: 50%;
	-webkit-animation: spin .6s linear infinite;
	        animation: spin .6s linear infinite;
}

@-webkit-keyframes spin {
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@keyframes spin {
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}


